.sidebar {
	width: percentage(220/940); }
.widget {
	margin-bottom: 35px;
	&__title {
		display: flex;
		align-items: center;
		margin-bottom: 18px;
		color: #e8603c;
		font-size: 14px;
		a {
			color: #e8603c; } }
	.delimiter {
		margin-left: 16px; }
	&__inner {
		display: flex;
		flex-direction: column;
		a {
			@include helvetica;
			color: #000;
			font-size: 12px; } } }

.gallery {
	display: grid;
	grid-template-columns: auto auto auto auto; }
