@include font('bebas',bebas-webfont);

@mixin bebas {
	font-family: bebas,sans-serif;
	font-weight: 400; }
@mixin helvetica {
	font-family: Helvetica Neue, sans-serif; }
a {
	text-decoration: none; }
body {
	@include bebas;
	@include smooth-font;
	background-image: url(../img/bg.jpg);
	background-size: cover; }

