.post-feed {
	width: percentage(660/940);
	margin-right: 62px;

	.delimiter {
		margin-bottom: 45px; } }


.post-icon {
	position: absolute;
	margin-left: -85px;
	top: 0;
	left: 0; }
.icon-block {
	position: relative;
	height: 75px;
	width: 75px;
	background-color: #e8663c;
	.icon {
		@include vcenter;
		width: 32px;
		height: 32px;
		fill: #fff; } }
.post {
	position: relative;
	margin-bottom: 35px;
	&__wrapper {
		display: flex;
		margin-top: 12px; }
	.informers {
		width: percentage(150/670);
		margin-top: 3px;
		&__title {
			font-size: 12px;
			color: #e8603c;
			text-transform: uppercase;
			padding-bottom: 9px; }
		&__content {
			@include helvetica;
			color: #c3c3c3;
			font-size: 12px;
			line-height: 18px;
			padding-bottom: 8px; }
		&__tag {
			color: #c3c3c3; }
		&__counter {
			color: #c3c3c3; } }

	&__content {
		width: percentage(520/670);
		.title {
			padding-bottom: 16px; }
		p {
			line-height: 18px; } } }


.preview {
	&__img {
		object-fit: cover;
		width: 100%; } }
