html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-file {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-link {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 0.84em;
  height: 1em;
  fill: #FFF;
}

.icon-post {
  width: 0.85em;
  height: 1em;
  fill: initial;
}

.icon-quote {
  width: 1.32em;
  height: 1em;
  fill: #231F20;
}

.icon-slide {
  width: 1em;
  height: 1em;
  fill: null;
}

@font-face {
  font-family: "bebas";
  src: url("../fonts/bebas-webfont.woff2") format("woff2"), url("../fonts/bebas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

a {
  text-decoration: none;
}

body {
  font-family: bebas,sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(../img/bg.jpg);
  background-size: cover;
}

.page-wrapper {
  margin: 0 auto;
  max-width: 940px;
}

.content-wrapper {
  display: flex;
  margin-top: 23px;
}

.out {
  overflow: hidden;
}

.title h2 {
  font-size: 18px;
  color: #e8603c;
  text-transform: uppercase;
}

.text p {
  font-family: Helvetica Neue, sans-serif;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.03em;
  margin-bottom: 15px;
  color: #000;
}

.subtitle a {
  font-family: Helvetica Neue, sans-serif;
  color: #c3c3c3;
  font-size: 12px;
}

.delimiter {
  content: "";
  background: red;
  flex: 1;
  background: url(../img/pattern.png) 0 0;
  height: 13px;
}

.header {
  font-family: bebas,sans-serif;
  font-weight: 400;
  margin-bottom: 17px;
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  pading-left: 10px;
  font-size: 36px;
  display: flex;
}

.logo__left-part {
  color: #e8603c;
}

.logo__right-part {
  color: #f3af9d;
}

.nav {
  padding-top: 5px;
}

.nav__link {
  color: #b9b9b9;
}

.nav__link:not(:last-child) {
  margin-right: 26px;
}

.nav__link:hover {
  color: #e8603c;
}

.is-active {
  color: #e8603c;
}

.page-title {
  position: relative;
}

.page-title:before, .page-title:after {
  position: absolute;
  height: 60px;
  background: #f2ae9c;
  content: " ";
  width: 1000px;
  top: 0;
}

.page-title:before {
  right: 100%;
  margin-right: 10px;
}

.page-title:after {
  left: 100%;
  margin-left: 10px;
}

.page-title h1 {
  color: #fff;
  background-color: #e8603c;
  padding-left: 17px;
  height: 60px;
  vertical-align: middle;
  padding-top: 12px;
  font-size: 24px;
}

.post-feed {
  width: 70.21277%;
  margin-right: 62px;
}

.post-feed .delimiter {
  margin-bottom: 45px;
}

.post-icon {
  position: absolute;
  margin-left: -85px;
  top: 0;
  left: 0;
}

.icon-block {
  position: relative;
  height: 75px;
  width: 75px;
  background-color: #e8663c;
}

.icon-block .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
  fill: #fff;
}

.post {
  position: relative;
  margin-bottom: 35px;
}

.post__wrapper {
  display: flex;
  margin-top: 12px;
}

.post .informers {
  width: 22.38806%;
  margin-top: 3px;
}

.post .informers__title {
  font-size: 12px;
  color: #e8603c;
  text-transform: uppercase;
  padding-bottom: 9px;
}

.post .informers__content {
  font-family: Helvetica Neue, sans-serif;
  color: #c3c3c3;
  font-size: 12px;
  line-height: 18px;
  padding-bottom: 8px;
}

.post .informers__tag {
  color: #c3c3c3;
}

.post .informers__counter {
  color: #c3c3c3;
}

.post__content {
  width: 77.61194%;
}

.post__content .title {
  padding-bottom: 16px;
}

.post__content p {
  line-height: 18px;
}

.preview__img {
  object-fit: cover;
  width: 100%;
}

.sidebar {
  width: 23.40426%;
}

.widget {
  margin-bottom: 35px;
}

.widget__title {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
  color: #e8603c;
  font-size: 14px;
}

.widget__title a {
  color: #e8603c;
}

.widget .delimiter {
  margin-left: 16px;
}

.widget__inner {
  display: flex;
  flex-direction: column;
}

.widget__inner a {
  font-family: Helvetica Neue, sans-serif;
  color: #000;
  font-size: 12px;
}

.gallery {
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.quote {
  position: relative;
  margin-bottom: 55px;
}

.quote__inner {
  display: flex;
  background-size: cover;
}

.quote__img {
  padding-left: 36px;
  padding-top: 15px;
}

.quote__img .icon-quote {
  fill: #e8663c;
}

.quote__text {
  padding: 16px;
  font-style: italic;
}

.quote .icon-quote {
  transform: translate(-50%, -50%) rotate(180deg);
}

.post-link {
  position: relative;
  margin-bottom: 55px;
}

.post-link .subtitle {
  margin-bottom: 15px;
}

.post-link .title {
  font-size: 12px;
  color: #e8603c;
  text-transform: uppercase;
  padding-bottom: 9px;
}

.pagination {
  margin-bottom: 20px;
}

.pagination__page {
  color: #fff;
  background-color: #f3b29d;
  padding: 2px 10px 5px 8px;
  font-size: 12px;
}

.is-current {
  background-color: #e8663c;
}

.slider {
  position: relative;
}

.slider__slide {
  background-size: cover;
  height: 0;
  padding-bottom: 46%;
}

.slider__next, .slider__prev {
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: 19px;
  cursor: pointer;
  z-index: 2;
  margin-top: -36px;
}

.slider__next {
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
